import { HttpClient } from '@angular/common/http';
import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { LocationsProvider } from '../../providers/locations/locations';


@Component({
  selector: 'page-locations',
  templateUrl: 'locations.html',
})
export class LocationsPage {
  locations: any;

  constructor(public navCtrl: NavController, private http: HttpClient, public locationsProvider: LocationsProvider) {
    this.http.get('assets/data/locations.json').subscribe(data => {
      this.locations = data['centers'];
    });

    this.locationsProvider.load().then(locations => {
      this.locations = locations;
    });
  }

  openLocation(location) {
    this.navCtrl.push('LocationDetailsPage', { location });
  }

}



